@import '../../vars.css';

.budgetListContainer {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    display: flex;
    justify-content: start;
    align-items: center;
    background: linear-gradient(135deg, #55aad5, #6d55d5);
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
  }

.mainContent {
    width: 975px;
    height: 650px;
    border-radius: 20px;
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.1);
    background: #FFF;
    border: 3px solid #ccc;
    flex-shrink: 0;
    padding: 20px;
    margin-right: 100px;
    position: relative;
  }

.header h1 {
    color: #3a3a3a;
    font-family: var(--font-roboto);
    font-size: 36px;
    font-weight: 700;
    margin-bottom: 20px;
    text-align: left;
    margin-left: 10px;
}

.budgetList {
  margin-top: 20px;
}

.budgetItem {
  background-color: #f5f5f5;
  border-radius: 8px;
  padding: 15px;
  margin-bottom: 10px;
}

.addButton {
  position: absolute;
  top: 20px;
  right: 20px;
  padding: 12px 25px;
  background-color: #6d55d5;
  color: white;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s, transform 0.2s;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.addButton:hover {
  background-color: #0056b3;
}

.deleteButton {
  padding: 6px 12px;
  background-color: #e74c3c;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s;
  margin-top: 10px;
}

.deleteButton:hover {
  background-color: #c0392b;
}
